.schedule-page {
  padding-top: 240px;
  &__inner {
    @include ms {
      padding: 0; } }
  &__head {
    display: flex;
    align-items: center;
    flex-wrap: wrap; }
  &__title {
    max-width: 33%;
    margin-right: auto;
    @include md {
      max-width: none;
      width: 100%; }
    @include ms {
      padding: 0 16px; } }
  &__list {
    padding-top: 60px; } }


.schedule-group {
  @extend %cardShadow;
  display: flex;
  flex-wrap: wrap;
  &__title {
    padding: 24px 20px;
    @include flex-size(100%); }
  &__item {
    @include flex-size(100%);
    @include md {
      @include flex-size(50%); } } }

.schedule {
  display: flex;
  border-top: 2px solid $grayBorder;
  @include lg {
    font-size: 13px; }
  @include md {
    flex-direction: column; }
  &__block-title {
    display: none;
    @include sm {
      display: block; } }
  & > * {
    padding: 30px 20px;
    &:not(:last-child) {
      border-right: 2px solid #f0f0f3; } }
  &__title {
    margin-bottom: 14px;
    color: $grayLight;
    letter-spacing: 0.033em; }
  &__content {
    font-weight: 700;
    &_price {
      display: flex;
      justify-content: space-between;
      align-items: flex-start; } }
  &__date {
    @include flex-size(11%);
    span {
      display: block; } }
  &__city {
      @include flex-size(14%); }
  &__direction {
    @include flex-size(11%); }
  &__instructors {
    @include flex-size(17%);
    flex-grow: 1;
    .instructor-preview {
      &:not(:last-child) {
        margin-bottom: 15px; } } }
  &__adress {
    @include flex-size(21%); }
  &__price {
    flex-grow: 1; } }

.instructor-preview {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  &__image {
    @include circle(40);
    @include cover;
    margin-right: 10px; } }
