.about-page {
	margin-top: 245px;
	@include md {
		margin-top: 100px; }
	&__inner {
		@include sm {
			padding: 0; } }
	&__main {
		display: flex;
		flex-wrap: wrap;
		align-items: flex-start; }
	&__gallery {
		width: 50%;
		@include lg {
			width: 100%; } }
	&__text {
		width: 50%;
		padding-left: 50px;
		@include lg {
			width: 100%;
			padding-left: 0;
			margin-top: 40px; }
		@include sm {
			padding: 0 16px; } }
	&__descr {
		margin-top: 30px;
		line-height: 2;
		@include sm {
			margin-top: 24px; } }
	&__head {
		display: flex;
		align-items: center;
		justify-content: space-between; }
	&__title {
		.about-news & {
			@include sm {
				padding: 0 16px; } } } }

.about-news {
	margin-top: 150px;
	@include sm {
		margin-top: 75px; }
	&__row {
		margin-top: 32px;
		@include sm {
			margin: 22px 0 -8px; } }
	&__col {
		padding: 8px 15px;
		@include md {
			padding: 8px 8px; }
		@include sm {
			padding: 8px 0; } } }

.about-testimonials {
	margin-top: 150px;
	@include md {
		margin-top: 80px; }
	@include sm {
		padding: 0 16px; }
	&__row {
		margin-top: 70px;
		@include md {
			margin-top: 40px; } } }

.about-partners {
	margin-top: 150px;
	@include md {
		margin-top: 80px; }
	@include sm {
		padding: 0 16px; }
	&__row {
		margin-top: 52px;
		margin-bottom: -8px;
		@include md {
			margin-top: 22px;
			margin-bottom: -8px; } }
	&__col {
		padding: 8px 15px;
		@include md {
			padding: 8px 8px; } } }
